@import url('https://fonts.googleapis.com/css2?family=Rowdies&display=swap');

.hero-container {
  max-width: 100%;
  width: 100%;
  background-color: black; /* Black background */
  position: relative;
  overflow: hidden; /* Prevent overflow */
  height: 800px;
}

/* Hero Navbar Styling */
.hero-navbar {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px #ECFDF3 solid;
  display: flex;
  justify-content: center; /* Center the navbar container */
  align-items: center;
  z-index: 2;
}

/* Navbar container for logo and links */
.hero-navbar .navbar-container {
  display: flex;
  justify-content: space-between; /* Space between logo and links */
  width: 100%;
  max-width: 1200px; /* Limit width for larger screens */
  align-items: center;
  padding: 0 20px;
}

/* Logo styling */
.hero-navbar .navbar-logo {
  width: 200px;
  height: 80px;
}

/* Navbar links styling */
.hero-navbar .navbar-links {
  display: flex;
  gap: 24px;
  justify-content: flex-end; /* Align links to the right */
  flex-grow: 1; /* Allow links to grow and take available space */
  text-align: center;
}

/* Link text styling */
.hero-navbar .navbar-links div {
  color: #D1D1D6;
  font-size: 16px;
  font-family: 'Rowdies', sans-serif;
  font-weight: 400;
  line-height: 20px;
  text-decoration: none; /* Remove text decoration */
}

.hero-navbar .navbar-links .navbar-link-active {
  color: white;
  font-weight: 700;
}

/* Hamburger Menu */
.hamburger-menu {
  display: none; /* Initially hidden */
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 30px;
  height: 21px;
  border: none;
  background: none;
  cursor: pointer;
}

.hamburger-line {
  width: 30px;
  height: 4px;
  background-color: white;
}

/* Hero Background */
.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  justify-content: space-between;
}

.hero-background .background-left,
.hero-background .background-right {
  position: absolute;
  height: 100%;
  background-size: cover;
  background-position: center;
}

.hero-background .background-left {
  left: 0;
  background-image: url('../assets/Group\ 1.png');
  width: 45%;
}

.hero-background .background-right {
  right: 0;
  background-image: url('../assets/logo.png');
  width: 50%;
  top: 10%;
}

/* Hero Content */
.hero-content {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 30px;
  padding-top: 170px;
  color: white;
  text-align: left;
  margin: 0 50px;
  width: 650px;
}

.hero-content .hero-title {
  font-size: 56px;
  font-family: 'Rowdies', sans-serif;
  font-weight: 700;
  line-height: 67.2px;
}

.hero-content .hero-subtitle {
  font-size: 18px;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  line-height: 27px;
}

.hero-content .hero-cta {
  padding-left: 24px;
  padding-right: 24px;
  padding-top: 12px;
  padding-bottom: 12px;
  border-radius: 10px;
  border: 1px solid white;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transition: background-color 0.3s ease, transform 0.3s ease; /* Adding transition for smooth hover effect */
  width: 80%;
}

/* Hover effect */
.hero-cta:hover {
  background-color: purple;
  transform: scale(1.05); /* Optional: Adds a slight scale effect */
}

.hero-content .hero-cta-text {
  font-size: 24px;
  font-family: 'Rowdies', sans-serif;
  font-weight: 700;
  line-height: 36px;
  width: 100%;
  text-align: center;
  text-decoration: none; /* Remove text decoration */
  color: white;
}

/* Media Queries for responsiveness */

/* Mobile Devices */
@media (max-width: 768px) {
  /* Hero Container */
  .hero-container {
    height: 600px;
  }

  /* Navbar */
  .hero-navbar .navbar-container {
    flex-direction: row;
    align-items: center;
    margin-bottom: -20px;
  }

  .hero-navbar .navbar-links {
    flex-direction: column;
    gap: 10px;
    justify-content: center;
    display: none; /* Hide navbar links by default */
    width: 100%;
    text-align: center;
    padding: 20px;
    background-color: black; /* Dark background for dropdown */
  }

  .hero-navbar .navbar-links.open {
    display: flex; /* Show navbar links when menu is open */
  }

  /* Hero Content */
  .hero-content {
    width: 100%;
    align-items: center;
    margin: 20px 0;
    padding-top: 80px;
    
    gap: 20px;
  }

  .hero-content .hero-title {
    font-size: 36px;
    line-height: 44px;
    text-align: center;
    padding: 20px;
  }

  .hero-content .hero-subtitle {
    font-size: 16px;
    width: 95%;
    text-align: center;
  }

  .hero-cta {
    width: 90%;
    padding: 10px;
  }

  .hero-cta-text {
    font-size: 18px;
  }

  .hero-background .background-left
 {
  position: absolute;
  height: 110%;
  background-size: cover;
  background-position: center;
  width: 40%;
}
.hero-background .background-right{
  display: none;
}

  /* Show Hamburger Menu */
  .hamburger-menu {
    display: flex; /* Show hamburger on mobile */
  }
}
