@import url('https://fonts.googleapis.com/css2?family=Rowdies&display=swap');

/* Main Container */
.main-container {
    max-width: 100%;
    width: 100%;
    height: auto;  /* Allow height to adjust based on content */
    background-color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;  /* Center vertically */
    align-items: center;  /* Center horizontally */
    gap: 80px;
    padding-bottom: 100px;
}

/* Title Section */
.title-section {
    text-align: center;
    color: white;
    font-size: 40px;
    font-family: 'Rowdies', sans-serif;
    font-weight: 700;
    line-height: 58px;
    word-wrap: break-word;
    padding: 0 20px;  /* Add some padding for mobile screens */
    width: 700px;
}

/* Features Wrapper */
.features-wrapper {
    align-self: stretch;
    display: flex;
    flex-direction: row;  /* Align features horizontally */
    justify-content: center; /* Center features horizontally */
    gap: 32px; /* Space between feature boxes */
    flex-wrap: wrap; /* Wrap to the next line if needed */
    justify-content: center; /* Ensure they are centered on mobile */
    width: 100%;
}

/* Single Feature Item */
.feature-item {
    flex: 1 1 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 16px;
    min-width: 280px;  /* Prevent features from shrinking too much */
    padding: 10px;
    text-align: center; /* Center text inside each feature */
}

/* Icon Container */
.icon-container {
    width: 48px;
    height: 48px;
    position: relative;
}

/* Icon Style */
.icon-style {
    width: 36px;
    height: 40px;
    left: 6px;
    top: 4px;
    position: absolute;
}

/* Text Container for Feature */
.feature-text-container {
    height: 130px;
    flex-direction: column;
    gap: 24px;
    display: flex;
    text-align: center; /* Center the text content */
}

/* Feature Title */
.feature-title-text {
    text-align: center;
    color: white;
    font-size: 24px;
    font-family: 'Rowdies', sans-serif;
    font-weight: 700;
    line-height: 33.6px;
    word-wrap: break-word;
}

/* Feature Description */
.feature-description-text {
    text-align: center;
    color: white;
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    line-height: 24px;
    word-wrap: break-word;
    width: 210px;
    align-items: center;
}

/* Group4 image */
.group4{
    position: absolute;
    height: 109%;
    background-size: cover;
    background-position: center;
    width: 20%;
    left: 0;
    z-index: 1;
}

/* Media Queries for Responsiveness */

/* For larger screens (desktop/laptop) */
@media screen and (min-width: 1200px) {
    .title-section {
        font-size: 50px;
        width: 700px;
    }

    .features-wrapper {
        gap: 32px;
    }

    .feature-item {
        min-width: 280px;  /* Ensure a minimum width for each feature */
    }

    .group4 {
        display: block;
    }
}

/* For tablets (portrait mode) */
@media screen and (max-width: 768px) {
    .title-section {
        font-size: 36px;
        width: 90%;  /* Ensure the title section fits well on smaller screens */
    }

    .features-wrapper {
        gap: 24px;
        flex-direction: column;
        justify-content: center;
    }

    .feature-item {
        min-width: 280px;
        width: 80%;
        text-align: center;
    }

    .group4 {
        display: none;  /* Hide the large image on tablet and smaller screens */
    }
}

/* For mobile devices (portrait mode) */
@media screen and (max-width: 480px) {
    .title-section {
        font-size: 28px;
        width: 90%;  /* Ensure the title section fits well on smaller screens */
    }

    .features-wrapper {
        gap: 16px;
        flex-direction: column;
        align-items: center;
    }

    .feature-item {
        min-width: 100%;
        width: 90%;  /* Take up most of the screen width */
    }

    .feature-title-text {
        font-size: 20px;
    }

    .feature-description-text {
        font-size: 14px;
    }

    .group4 {
        display: none;  /* Hide the large image on mobile screens */
    }
}

/* For very small screens (extra small mobile devices) */
@media screen and (max-width: 320px) {
    .title-section {
        font-size: 24px;
        line-height: 32px;
    }

    .feature-title-text {
        font-size: 18px;
    }

    .feature-description-text {
        font-size: 12px;
    }

    .group4 {
        display: none;  /* Hide the large image on very small devices */
    }
}
