@import url('https://fonts.googleapis.com/css2?family=Rowdies&display=swap');

.middle-container {
  max-width: 100%;
  width: 100%;
  height: 750px;
  background-color: black;
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.middle-inner-container {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  padding: 115px 64px 112px 64px;
  gap: 32px;
}

.middle-inner-container img {
  width: 100%;
  max-width: 630px;
  height: auto;
}

.middle-content-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 80px;
}

.middle-title-section {
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.middle-main-text-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.middle-main-title {
  color: white;
  font-size: 48px;
  font-family: 'Rowdies', sans-serif;
  font-weight: 700;
  line-height: 57.6px;
}

.middle-description {
  color: white;
  font-size: 18px;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  line-height: 27px;
}

.middle-button-wrapper {
  width: 310px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 24px;
}

.middle-button {
  padding-left: 24px;
  padding-right: 24px;
  padding-top: 12px;
  padding-bottom: 12px;
  border-radius: 10px;
  border: 1px solid white;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transition: background-color 0.3s ease, transform 0.3s ease;
  width: 100%;
  max-width: 600px;
}

.middle-button:hover {
  background-color: purple;
  transform: scale(1.05);
}

.middle-button-text {
  font-size: 24px;
  font-family: 'Rowdies', sans-serif;
  font-weight: 700;
  line-height: 36px;
  width: 520px;
  text-align: center;
  text-decoration: none;
  color: white;
}

.middle-image {
  width: 630px;
  height: 536px;
  object-fit: cover;
  margin-top: -10pc;
}

/* Media Queries for Responsiveness */
@media screen and (max-width: 1200px) {
  
  .middle-inner-container {
    padding: 80px 40px;
    flex-direction: column;
    align-items: center;
    gap: 40px;
   height: 650px;
  }

  .middle-inner-container img {
    max-width: 100%;
    height: auto;
  }

  .middle-content-section {
    gap: 60px;
  }

  .middle-main-title {
    font-size: 36px;
    line-height: 44px;
  }

  .middle-description {
    font-size: 16px;
  }

  .middle-button-wrapper {
    width: 100%;
    justify-content: center;
    gap: 16px;
  }

  .middle-button {
    width: 80%;
    max-width: 500px;
  }

  .middle-button-text {
    font-size: 20px;
  }

  .middle-image {
    width: 100%;
    height: 400px;
    margin-top: 0;
  }
}

@media screen and (max-width: 798px) {
  .middle-inner-container {
    padding: 60px 20px;
    height: 550px;
  }

  .middle-inner-container img {
    display: none;  /* Hide image on small screens */
  }

  .middle-content-section {
    gap: 40px;
  }

  .middle-main-title {
    font-size: 28px;
    line-height: 36px;
  }

  .middle-description {
    font-size: 14px;
  }

  .middle-button-wrapper {
    width: 100%;
    justify-content: center;
    gap: 8px;
  }

  .middle-button {
    width: 80%;
    max-width: 400px;
  }

  .middle-button-text {
    font-size: 18px;
  }
}

@media screen and (max-width: 480px) {
  .middle-inner-container {
    padding: 40px 10px;
    height: 350px;
  }

  .middle-content-section {
    gap: 30px;
   
  }

  .middle-main-title {
    font-size: 24px;
    line-height: 32px;
     padding: 30px;
  }

  .middle-description {
    font-size: 12px;
    padding: 30px;
  }

  .middle-button-wrapper {
    width: 100%;
    justify-content: center;
    gap: 4px;
    
  }

  .middle-button {
    width: 80%;
    max-width: 320px;
    
  }

  .middle-button-text {
    font-size: 16px;
  }
}
