@import url('https://fonts.googleapis.com/css2?family=Rowdies&display=swap');

.container {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 80px;
  background-color: black;
  padding: 40px 20px;
  box-sizing: border-box;
  overflow-x: hidden; /* Prevent horizontal overflow */
 
}

.title-text-container {
  display: flex;
  flex-direction: column; /* Stack on smaller screens */
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  gap: 20px;
  
}

.left-column {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 16px;
  flex: 1;
  width: 100%;
  margin-left: 60px;
}

.title {
  color: white;
  font-size: 48px;
  font-family: 'Rowdies', sans-serif;
  font-weight: 700;
  line-height: 57.6px;
  word-wrap: break-word;
  width: 100%;
  margin: 0;
  padding: 0;
}

.text {
  color: white;
  font-size: 18px;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  line-height: 27px;
  word-wrap: break-word;
  flex: 1;
  text-align: left;
  padding: 40px;
  margin: 0;
  width: 90%;
}

/* Features section */
.feature-container {
  width: 100%;
  display: flex;
  flex-direction: column; /* Stack features on small screens */
  justify-content: flex-start;
  align-items: center;
  gap: 35px;
  margin-top: 40px;
  flex-wrap: wrap; /* Allow wrapping on small screens */
  
}

.feature {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 20px;
  flex: 1;
  padding: 20px;
  width: 100%;
  max-width: 250px;
  margin-bottom: 30px;
  transition: all 0.3s ease-in-out;
  box-sizing: border-box;
  margin-left: 60px;
}

.feature:hover {
  transform: scale(1.05); /* Hover effect */
}

.icon-container {
  width: 48px;
  height: 48px;
  position: relative;
}

.icon {
  width: 36px;
  height: 40px;
  position: absolute;
  left: 6px;
  top: 4px;
}

.feature-title {
  text-align: center;
  color: white;
  font-size: 32px;
  font-family: 'Rowdies', sans-serif;
  font-weight: 700;
  line-height: 41.6px;
  word-wrap: break-word;
  width: 100%;
}

.feature-description {
  text-align: center;
  color: white;
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  line-height: 24px;
  word-wrap: break-word;
  width: 100%;
}

/* Group2 image */
.group2 {
  position: absolute;
  height: 150%;
  background-size: cover;
  background-position: center;
  width: 30%;
  right: 0;
  top: 370%;
  transform: translateY(-50%);
  transition: all 0.3s ease;
}

/* Media Queries for Responsiveness */

/* For larger screens (desktop/laptop) */
@media screen and (min-width: 1200px) {
  .title-text-container {
    flex-direction: row; /* Display title and description side by side */
    align-items: center;
    gap: 40px;
  }

  .left-column {
    flex-direction: row;
    gap: 40px;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
  }

  .title {
    font-size: 48px;
    line-height: 57.6px;
  }

  .text {
    font-size: 18px;
  }

  .feature-container {
    gap: 35px;
    flex-direction: row; /* Align features horizontally */
    flex-wrap: nowrap;
  }

  .feature {
    max-width: 250px;
    margin-bottom: 40px;
  }

  .group2 {
    width: 30%;
  }
}

/* For tablets (portrait mode) */
@media screen and (max-width: 768px) {
  .title-text-container {
    flex-direction: column; /* Stack title and description vertically */
  }

  .title {
    font-size: 36px;
    line-height: 44px;
  }

  .text {
    font-size: 16px;
    width: 85%;
  }

  .feature-container {
    gap: 25px;
    flex-direction: column;
    align-items: center;
  }

  .feature {
    max-width: 300px;
    width: 90%;
    margin-left: 0px;
  }

  .group2 {
    width: 45%;
  }

  .container{
    height: auto;
  }
}

/* For small screens (portrait/mobile) */
@media screen and (max-width: 480px) {
  .container {
    padding: 20px 10px;
  }

  .title {
    font-size: 28px;
    line-height: 36px;
  }

  .text {
    font-size: 14px;
  }

  .feature-container {
    gap: 20px;
    flex-direction: column;
    align-items: center;
  }

  .feature {
    max-width: 100%;
    width: 90%;
    margin-bottom: 20px;
  }

  .feature-title {
    font-size: 24px;
  }

  .feature-description {
    font-size: 14px;
  }

  .group2 {
    display: none; /* Hide the group2 image on small devices */
  }
  .left-column{
    margin-left: 40px;
  }
}

/* For very small screens (extra small mobile devices) */
@media screen and (max-width: 320px) {
  .title {
    font-size: 24px;
    line-height: 32px;
  }

  .text {
    font-size: 12px;
  }

  .feature-title {
    font-size: 22px;
  }

  .feature-description {
    font-size: 12px;
  }
}
