@import url('https://fonts.googleapis.com/css2?family=Rowdies&display=swap');

/* Footer Container */
.footer-container {
    display: flex;
    flex-direction: column;
    gap: 32px;
    max-width: 100%;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    background-color: black;
    position: relative;
    height: auto;
   
}

/* Outer Footer Section with White Border */
.footer-outer-container {
    width: 90%;
    padding: 48px;
    border: 1px solid white;
    display: flex;
    flex-wrap: wrap;  /* Allow wrapping on smaller screens */
    justify-content: space-between;
    gap: 32px;
}

/* Left Column for Subscription Section */
.footer-left-column {
    width: 100%;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    gap: 24px;
    flex: 1;
}

.footer-left-column img {
    width: 250px;
    height: 151px;
    object-fit: contain;
}

.footer-subscribe-text {
    color: white;
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    line-height: 24px;
    word-wrap: break-word;
}

/* Subscription Form */
.footer-subscription-form {
    display: flex;
    flex-direction: column;
    gap: 12px;
    height: 78px;
    z-index: 100;
   
}

.footer-input-container {
    display: flex;
    gap: 16px;
    align-items: center;
}

.footer-input-box {
    flex: 1;
    height: 48px;
    padding: 12px;
    border: 1px solid white;
    color: white;
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
    background-color: transparent;
    outline: none;
}

.footer-input-box::placeholder {
    color: white;
    opacity: 0.6;
}

.footer-subscribe-button {
    height: 48px;
    padding: 12px 24px;
    border: 1px solid white;
    background-color: transparent;
    color: white;
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
    cursor: pointer;
    transition: background-color 0.3s;
}

.footer-subscribe-button:hover {
    background-color: white;
    color: black;
}

/* Terms Text */
.footer-terms-text {
    color: white;
    font-size: 12px;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    line-height: 18px;
    word-wrap: break-word;
    margin-bottom: 20px;
}

/* Right Column for Quick Links and Connect Section */
.footer-right-column {
    display: flex;
    flex-direction: row;
    gap: 32px;
    flex: 1;
}

.footer-right-column .footer-title {
    color: white;
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
    font-weight: 600;
    line-height: 24px;
    word-wrap: break-word;
}

.footer-quick-links {
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin-top: 40px;
}

.footer-quick-links .footer-link {
    color: white;
    font-size: 14px;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    line-height: 21px;
    word-wrap: break-word;
    padding: 8px 0;
    cursor: pointer;
    transition: color 0.3s;
}

.footer-quick-links .footer-link:hover {
    color: #f1f1f1;
}

/* Connect With Us Section */
.footer-connect-with-us {
    display: flex;
    flex-direction: column;
    gap: 12px;
    margin-top: 40px;
}

.footer-connect-item {
    display: flex;
    gap: 16px;
    align-items: center;
}

.footer-connect-item img {
    width: 28px;
    height: 28px;
}

/* Footer Text Section */
.footer-text {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 95%;
    color: white;
    font-size: 14px;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    line-height: 21px;
    word-wrap: break-word;
    padding: 16px 20px;
}

/* Group3 Image */
.group3 {
    position: absolute;
    height: 70%;
    width: 20%;
    top: 30%;
    right: 0;
    background-size: cover;
    background-position: center;
    z-index: 1; /* Ensure it stays behind the text */
}

/* Media Queries for Responsiveness */

/* Tablets (portrait mode) */
@media screen and (max-width: 768px) {
    .footer-outer-container {
        flex-direction: column;
        padding: 20px;
    }

    .footer-left-column {
        width: 100%;
        max-width: none;
    }

    .footer-right-column {
        width: 100%;
        gap: 16px;
    }

    .group3 {
        width: 30%;
        top: 10%;
    }
}

/* Mobile */
@media screen and (max-width: 480px) {
    .footer-left-column img {
        width: 150px;
    }

    .footer-input-box {
        font-size: 14px;
    }

    .footer-subscribe-button {
        font-size: 14px;
    }

    .footer-quick-links .footer-link {
        font-size: 12px;
    }

    .footer-connect-item img {
        width: 24px;
        height: 24px;
    }

    .group3 {
        display: none;
    }
}
